body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.app {
  color: #cdcdcd;
  font-size: 16px;
  font-family: 'Roboto';
  font-weight: 300;
}

.header {
  text-align: left;
  height: 40px;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: 24px;
  background-color: #111;
  /* background-image: -webkit-linear-gradient(top,#282828,#191919); */
}

.footer{
  height: 20px;
  padding: 10px;
  text-align: center;
  font-size: 90%;
  background-color: #111;
  /* background-image: -webkit-linear-gradient(top,#282828,#191919); */
}

.logo {
  padding: 0 10px;
}
.menu {
  -webkit-flex-grow: 1;
          flex-grow: 1;
  padding: 0 10px;
}

.body {
  height: calc(100vh - 40px);
  display: -webkit-flex;
  display: flex;
  background-color: #262626;
}

.container{
  padding: 10px;
  margin: 10px;
  display: -webkit-flex;
  display: flex;
}

.ship-icon {
  font-size: 28px;
}

.squad-icon img {
  height: 28px;
  margin: 5px;
}

.list {
  width: 500px;
  background-color: #222222;
  background-image: -webkit-linear-gradient(left,#282828,#222222);
}

.row {
  display: -webkit-flex;
  display: flex;
}

.cell {
  padding: 5px;
}
.selected {
  background-color: rgba(80,80,80,.5);
}

.block{
  max-width: 500px;
}

select {
  margin: 5px;
  padding: 5px;
  width: 300px;
  background-color: #2a2a2a;
  color: #cdcdcd;
  border: 1px solid #3e3e3e;
  border-radius: 4px;
}

button {
  padding: 10px;
  background-color: #2a2a2a;
  color: #cdcdcd;
  border: 1px solid #3e3e3e;
  border-radius: 4px;
}

input {
  padding: 5px;
  background-color: #2a2a2a;
  color: #cdcdcd;
  border: 1px solid #3e3e3e;
  border-radius: 4px;
}

select:focus, button:focus{
  outline:0;
}

p{
  font-size: 14px;
  margin-left: 10px;
  margin-right: 10px;
}

.fluff {
  font-style: italic;
  font-size: 90%;
}

.title {
  font-weight: 500;
}

.white {
  color: #ddd;
}

.blue {
  color: #2989ff;
}

.red {
  color: rgb(255, 75, 75);
}

.green {
  color: rgb(75, 192, 75);
}

.yellow {
  color: rgb(245, 201, 58);
}

.pilot-skill {
  font-size: 24px;
  font-weight: bold;
  color: rgb(230, 145, 19);
}

.card-image {
  display: -webkit-inline-flex;
  display: inline-flex;
  height: 30px;
}
